import {CoreSounds} from "core/assets"
import {AssetUtl} from "core/assets/assetUtl"
import {Sys} from "core/logic/sys"
import {audioService} from "core/services"
import {GameComponentContext} from "core/types"
import {Signals} from "core/ui/animations/signals"
import {AnswerControlType} from "core/ui/components/answers/answerControlType"
import {GameComponent} from "core/ui/components/games/gameComponent"
import {Img} from "core/ui/controls"
import {screenSizeService} from "core/ui/services/instances"
import {Dev} from "dev"
import {toJS} from "mobx"

const bgLeftKey = "bg_left"

const introInterval = 2000
const introDelay = 300

export const durations = {
  introAnimationDelay: 100,
  introInterval,
  introDelay
}

export abstract class BaseMainGameComponent<S = {}> extends GameComponent<{}, S> {

  statusOptions = {
    offset: 0,
    waitCompleteBeforeNewTask: false,
  }

  introOptions = {
    disableSound: false,
    runOnNewTaskAfter: false
  }

  initialState(): S {
    return {} as any
  }

  answersControl = AnswerControlType.None

  constructor(context: GameComponentContext) {
    super(context)
    context.uiService.rootComponent = this
    context.gameState = this.initialState()

    this.fixedToCamera = true
    this.onDestroy.addOnce(() => {
      this.context.uiService.mainComponent.onNewTask.dispose()
      this.context.uiService.mainComponent.onAnswer.dispose()
      audioService.stopAll()
    })

    this.onChangeForRun(
      x => screenSizeService.screenSize,
      x => {
        this.cameraOffset.y = screenSizeService.topOffset
      })

  }

  get cameraBottom() {
    const bottom = screenSizeService.height - this.cameraOffset.y
    return bottom
  }

  prepareDefaultIntroAnimation(delay = durations.introDelay) {
    let bgLeftOffset = 0
    let bgLeft: Img | undefined = undefined
    if (!screenSizeService.isPortrait && AssetUtl.checkImageKey(this.gameKey(bgLeftKey))) {
      bgLeft = this.addImage([bgLeftKey, [0, 0]])
      const width = -bgLeft.width + 2
      bgLeft.x = width
      bgLeftOffset = -width
    }
    this.cameraOffset.x = -screenSizeService.realLeftOffset + bgLeftOffset
    const tween = this.game.add.tween(this.cameraOffset).to(
      {x: 0}, durations.introInterval, Phaser.Easing.Cubic.InOut, false, delay
    )
    if (bgLeft) {
      tween.onComplete.addOnce(() => this.remove(bgLeft, true))
    }
    return tween
  }

  introAnimation(): { start: () => void, onComplete: Phaser.Signal } {
    const tween = this.prepareDefaultIntroAnimation()
    return tween
  }

  startIntro() {
    if (Sys.devMode && !Dev.devModeIntro) {
      this.context.uiService.mainComponent.onIntroComplete.dispatch()
      return Signals.memorize()
    }
    if (!this.introOptions.disableSound) {
      this.playSound(CoreSounds.intro, false, true)
    }
    const tween = this.introAnimation()
    const signal = Signals.bind(this.delay(durations.introAnimationDelay), () => {
      tween.start()
      return tween.onComplete
    })
    signal.addOnce(() => this.context.uiService.mainComponent.onIntroComplete.dispatch())
    this.disableInputOn(signal)
    return signal
  }

  onAfterInit() {
    this.initTaskRendering()
  }

  private initTaskRendering() {

    this.onChangeFor(() => this.gameState.lastAnswer, x => {
      this.context.uiService.mainComponent.onAnswer.dispatch(x)
    })
    this.onChangeFor(() => this.context.gameService.gameCycleService.state.levelTask.task,
      x => {
        if (this.statusOptions.waitCompleteBeforeNewTask) {
          this.disableInputOn(this.context.uiService.answerStatus.onAnimationComplete).addOnce(() => {
            this.callRenderNewTask(x)
          })
        } else {
          this.callRenderNewTask(x)
        }
      })
    this.callRenderNewTask(this.context.gameService.gameCycleService.state.levelTask.task)
  }

  private callRenderNewTask(task: any) {
    if (this.introOptions.runOnNewTaskAfter && this.isFirstTask) {
      this.context.uiService.mainComponent.onIntroComplete.addOnce(() => {
        this.context.uiService.mainComponent.onNewTask.dispatch(toJS(task))
      })
    } else {
      this.context.uiService.mainComponent.onNewTask.dispatch(toJS(task))
    }
  }

  playBackgroundLoop() {
    this.context.uiService.mainComponent.onIntroComplete.addOnce(() => this.playSound("bg", true))
  }

}



