import {GameLogic} from "core/logic"
import {Sys} from "core/logic/sys"
import {RandomGenerator} from "core/utils"
import {Level} from "./levels"

export type Task = {
  left: number
  right: number
  answers: string[]
}

export class Logic extends GameLogic<Task, Level> {
  rng = new RandomGenerator()

  nextTask(level: Level) {
    const left = this.rng.int(10 * level.numberLength)
    const right = this.rng.int(10 * level.numberLength)
    const answers = [left > right ? "left" : "right"]

    const task: Task = {
      left,
      right,
      answers
    }
    return task
  }

}





