import {Sizes} from "core/ui/common/sizes"
import {BaseMainGameComponent} from "core/ui/components/games/baseMainGameComponent"
import {OnInit} from "core/ui/components/onInit"
import {Btn} from "core/ui/controls/btn"
import {Images} from "../assets"
import {QuestionComponent} from './questionComponent'
import {Task} from '../logic'

const durations = {}

export class MainComponent extends BaseMainGameComponent implements OnInit {
  positions = Sizes.create({
    question: {x: 800, y: 400},
  })
  biPositions = Sizes.bi({
    left: {x: 1200, y: 400},
    right: {x: 1300, y: 400}
  }, {
    left: {x: 800, y: 800},
    right: {x: 900, y: 800}
  })

  bg = this.addImage(Images.bg)

  question = this.add(this.createComponent(QuestionComponent)).setPos(this.positions.question)

  createAnswerBtn(right: boolean) {
    return this.addAnswerBehavior(this.add(new Btn(right ? "<" : ">", () => {
    })), right ? "right" : "left", {})
  }

  onInit(): void {
    this.createAnswerBtn(true).setBiPos(this.biPositions.left)
    this.createAnswerBtn(false).setBiPos(this.biPositions.right)
    this.startIntro()
  }

  onNewTask(task: Task) {

  }

}




