import {IGameMetaData} from "core/logic"
import {LevelOptions, ScoreOptions} from "core/types"
import {Images, Sounds} from "./assets"
import {MainComponent} from "./components/mainComponent"
import {levels} from "./levels"
import {Logic} from "./logic"

export const gameId = "template"
const scoreOptions: ScoreOptions = {
  scoreAnswer: 100,
  scoreBonus: 0,
  scoreBonusPerLevel: 5
}
const levelOptions: LevelOptions = {
  levels,
  levelsSeqCounts: [{seqCount: 3, maxLevel: 100}]
}
export const metaData: IGameMetaData = {
  gameId,
  title: "Template",
  logic: Logic,
  component: MainComponent,
  scoreOptions,
  levelOptions,
  darkBg: true,
  images: Images.data,
  sounds: Sounds.keys,
  atlasCount: 1,
  tutorialTaskCount: 3,
  size: {
    width: 2048,
    height: 1280,
    heightMin: 1152
  }
}




